
import { getProductDetail, getCommentList, getRecommendList } from '@/api/detail.js'
import { submitPreOrder,getProductBillImage } from '@/api/order'
import { getCoustemURL, setOwnInfo,getTicket } from '@/api/user'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import Popup from 'vux/src/components/popup/index.vue'

import noData from '@/components/noData.vue'
import CountDown from 'vue2-countdown'
const list = () => ['产品详情', '用户评价', '产品推荐']
var ShoppingCar = window.localStorage.getItem('ShoppingCarStr')
var ShoppingCarStr = JSON.parse(ShoppingCar)
var choiceSpec = document.getElementById('choiceSpec')
var isLogin = window.localStorage.getItem('accountIsLogin')
export default {
name: 'homeIndex',
components: {
Swiper,
SwiperItem,
TabItem,
XNumber,
Tab,
Sticky,
Divider,
noData,
Popup,
CountDown,
scrollTop: 0
},
data () {
return {
popupStyle: {
background: "#fff"
},
swipetoastShow:false,
showBigCommentImg: false,
nowCurrentImg: "",
couponShow: false,
lev: '',
visible: false,
top: 0,
left: 0,
list2: list(),
bannerlist: [],
demo2: '产品详情',
selectedTag: {},
curTab: 0,
valuenum: 1,
show: false,
feightfee: '',
actives: '',
commentlist: '',
goodrate: '',
commentnum: '',
skuid: '',
productid: '',
activetype: '',
ruleTrial: false,
additionist: [],
additionistCur: [],
carnum: 0,
orderDetail: {
skulist:[],
ticketlist:[],
cangetticketlist:[],
},
recommendskulist: [],
submitData: {// 提交结算数据
jsonstr: ''
},
addcarImg: '',
addcaranmation: false,
billimglist: [
{
img: 'static/images/aaa.png'
}
],
billimgurl:'',
questioninfo:'',
}
},
filters: {
format (value) {
console.log(value)








if (value.name) {
return value.name
}
},
typeFilter(value){
let res = value
switch (value) {
case '1':
res = '秒杀'
break;
case '2':
res = '拼团'
break;
case '3':
res = '试用'
break;
case '4':
res = '预售'
break;
case '5':
res = '买赠'
break;
case '6':
res = '折扣'
break;
case '7':
res = '会员买赠'
break;
case '8':
res = '会员折扣'
break;
case '9':
res = '品牌大使'
break;
default:
break;
}
return res
}
},
computed: {
actFilter () {
console.log(this.actives[0])
const str = this.actives[0]

console.log(str)

}
},
methods: {
showCommentImg(img) {
this.nowCurrentImg = img
this.showBigCommentImg = true
},
checkCoupon (checkid,index) {
console.log(checkid)
console.log(index)
const postData = {
id: checkid
}
getTicket(postData)
.then(res => {
if (res.data.errorcode === 0) {
this.$vux.toast.text('领取成功', 'middle')
this.orderDetail.cangetticketlist[index].isgot = 1;
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.toast.text('网络异常！', 'middle')
})

},

clickService () {
const _this = this
const postData = {skuid:this.skuid}
getCoustemURL(postData)
.then(res => {
if (res.data.errorcode === 0) {

window.location.href = res.data.data.url
} else {
_this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
_this.$vux.loading.hide()
console.log(err)
_this.$vux.toast.text('网络异常！', 'middle')
})
},
activeEndtime (row) {
if (this.activetype !== '') {
var t = new Date(row.replace(/-/g, '/'))
t = Date.parse(t)
return t
}
},
nowdate () {
var date = new Date() 
var str = date.getTime()
return str
},
close: function () {
this.show = false

},
goCard () {
this.$router.push({
name: 'shoppingtrolley'
})
},
getBillImg(){
if (localStorage.getItem('accountIsLogin') !== 'true')
{
this.$router.push({
name: 'login'
})
return
}
else{

const _this = this
const postData = {skuid:this.skuid}
getProductBillImage(postData)
.then(res => {
if (res.data.errorcode === 0) {

_this.billimgurl = res.data.data.url
_this.swipetoastShow = true

} else {
_this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
_this.$vux.loading.hide()
console.log(err)
_this.$vux.toast.text('网络异常！', 'middle')
})
}
},
spec: function () {
this.show = true

},
addlist (row, index) {
if (this.additionistCur[index] && this.additionistCur[index].id === row.id) {
let addlist = {id: '', name: '', pirce: ''}
this.$set(this.additionistCur, index, addlist)
return
}
this.$set(this.additionistCur, index, row)
},
recommend (skuid, productid,type) {
this.skuid = skuid
this.productid = productid
this.getDetail()
this.getComment()
this.getRecommend()
if (type == 1)
{
document.body.scrollTop = 0;
document.documentElement.scrollTop = 0;
}
},
endtimeorder () {
this.getDetail()
this.getComment()
this.getRecommend()
},
getDetail () {
const that = this
const postData = {
skuid: that.skuid,
productid: that.productid,
activetype: that.activetype,
}
getProductDetail(postData)
.then(res => {
if (res.data.errorcode === 0) {
that.orderDetail = res.data.data
that.feightfee = res.data.data.feight.fee
that.actives = res.data.data.actives
that.additionist = res.data.data.additionist
that.activetype = res.data.activetype
that.addcarImg = res.data.data.picurl[0]
that.questioninfo = res.data.data.questioninfo

let listStr = {name: '', id: '', price: ''}
that.additionist.map((item, index) => {
that.additionistCur.push(listStr)
})
var list = []
res.data.data.picurl.map((item, index) => {
let listurl = {}
listurl.img = item



list.push(listurl)
})
that.bannerlist = list
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.toast.text('网络异常！', 'middle')
})
},
getComment () {
const that = this
const postData = {
skuid: that.skuid,
productid: that.productid
}
getCommentList(postData)
.then(res => {
if (res.data.errorcode === 0) {
that.commentlist = res.data.data.commentlist
that.goodrate = res.data.data.goodrate
that.commentnum = res.data.data.commentnum
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.toast.text('网络异常！', 'middle')
})
},
getRecommend () {
const that = this
const postData = {
skuid: that.skuid,
productid: that.productid
}
getRecommendList(postData)
.then(res => {
if (res.data.errorcode === 0) {
this.recommendskulist = res.data.data.recommendskulist
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.toast.text('网络异常！', 'middle')
})
},
addCard (skuid) {


if (this.show === true) {
this.addcaranmation = true

if (ShoppingCarStr === null || ShoppingCarStr === undefined || ShoppingCarStr === '') {
ShoppingCarStr = new Array()
window.localStorage.setItem('ShoppingCarStr', JSON.stringify(ShoppingCarStr))

ShoppingCar = window.localStorage.getItem('ShoppingCarStr')
ShoppingCarStr = JSON.parse(ShoppingCar)
}
var productindex = -1
ShoppingCarStr.forEach((item, index) => {

if (Number(item.skuid) === Number(skuid)) {
productindex = index
}
})

var addlistStr = []
this.additionistCur.map(item => {
let strAdd = {}
if (item.id != '') {
strAdd.addid = item.id
addlistStr.push(strAdd)
}
})
if (productindex == -1) {

var ordernewadd = new Object()
ordernewadd.skuid = this.skuid
ordernewadd.num = this.valuenum
ordernewadd.addlist = addlistStr
ShoppingCarStr.push(ordernewadd)
window.localStorage.setItem('ShoppingCarStr', JSON.stringify(ShoppingCarStr))
} else {

ShoppingCarStr[productindex].addlist = addlistStr
ShoppingCarStr[productindex].num = ShoppingCarStr[productindex].num * 1 + this.valuenum
window.localStorage.setItem('ShoppingCarStr', JSON.stringify(ShoppingCarStr))
}
var numcar = 0
ShoppingCarStr.forEach((item, index) => {
numcar = ShoppingCarStr[index].num * 1 + numcar * 1
})
if (numcar > 99) {
numcar = '...'
}
this.carnum = numcar
const that = this
setTimeout(function () {
that.addcaranmation = false
}, 1200)
} else {
this.show = true
}






},
topaybtn (nogroup) {
if(this.show === true){
var outarr = new Array()
var arr = new Object()
var addlist = new Array()
arr.skuid = this.skuid
arr.num = this.valuenum
if (nogroup === 1) {
arr.nogroup = nogroup
}
this.additionistCur.map(item => {
let strAdd = {}
if (item.id != '') {
strAdd.addid = item.id
addlist.push(strAdd)
}
})
arr.addlist = addlist
outarr.push(arr)
this.submitData.jsonstr = JSON.stringify(outarr)
this.submitOrderData()
}
else {
this.show = true
}
},
submitOrderData () {
submitPreOrder(this.submitData)
.then(res => {
if (res.data.errorcode === 0) {




if (window.location.hostname === '0.0.0.0' || window.location.hostname === 'localhost') {

this.$router.push({path: '/confirmOrder', query: {preid: res.data.data.preid}})
} else {
window.location.href = process.env.BASE_SELF + '/wap/index.html#/confirmOrder?preid=' + res.data.data.preid
}







} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.toast.text('网络异常！', 'middle')
})
},
showCoupon () {
console.log(111111)
this.couponShow = true
},
stopPrevent (e) {
return false
},
subquestion(){
this.$router.push({path: '/orderDetail/issue', query: {'productid': this.productid,'skuid': this.skuid}})
},
seeallquestion(){
this.$router.push({path: '/orderDetail/questions', query: {'productid': this.productid,'skuid': this.skuid}})
},
questiondetail(id,text){
window.localStorage.setItem("questiontext",text)
this.$router.push({path: '/orderDetail/questionsdetail', query: {'questionid': id,'productid': this.productid,'skuid': this.skuid}})
}
},
created () {

setOwnInfo()
.then(res => {
if (res.data.errorcode === 0) {

this.lev = res.data.data.level
} else {

}
})
.catch(err => {
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
this.skuid = this.$route.query.skuid
this.productid = this.$route.query.productid
this.activetype = this.$route.query.activetype
this.getDetail()
this.getComment()
this.getRecommend()
ShoppingCar = window.localStorage.getItem('ShoppingCarStr')
ShoppingCarStr = JSON.parse(ShoppingCar)
if (!ShoppingCarStr && ShoppingCarStr !== null && ShoppingCarStr !== []) {
var oldcarnum = 0
ShoppingCarStr.forEach((item, index) => {
oldcarnum = ShoppingCarStr[index].num * 1 + oldcarnum * 1
})
this.carnum = oldcarnum
if (this.carnum > 99) {
this.carnum = '...'
}
}
},
}



